import { adapters } from "@rp/adapters"

type CustomerIoEventProperties = {
  [x: string]:
    | boolean
    | string
    | number
    | Array<string>
    | Array<Record<string, string | boolean>>
    | null
}

type dataProps = { name: string; data: CustomerIoEventProperties }

export const homePageCustomerIo = {
  sendScrollCarousel(data: dataProps) {
    adapters.customerIo.track({
      name: data.name,
      data: data.data,
    })
  },

  sendCardClickedOn(data: dataProps) {
    const message: { name: string; data: CustomerIoEventProperties } = {
      name: data.name,
      data: data.data,
    }
    adapters.customerIo.track(message)
  },
}

export const onboardingModalCustomerIo = {
  sendNextButtonClickedOn(data: dataProps) {
    adapters.customerIo.track({
      name: data.name,
      data: data.data,
    })
  },

  sendBackButtonClickedOn(data: dataProps) {
    const message: { name: string; data: CustomerIoEventProperties } = {
      name: data.name,
      data: data.data,
    }
    adapters.customerIo.track(message)
  },
}
